import React from 'react'
import { useNavigate } from 'react-router-dom'
import { Form, Input, Button, DatePicker, Picker, TextArea, Radio, Space } from 'antd-mobile';
import dayjs from 'dayjs';
// 学习模块选项
const moduleColumns = [
    { label: '行测', value: 'xingnce' },
    { label: '申论', value: 'shenlun' },
]

// 任务类型选项
const taskTypeColumns = [
    { label: '模考', value: 'exam' },
    { label: '真题', value: 'relaExam' },
    { label: '日常练习', value: 'practice' },
    { label: '专项训练', value: 'training' },
]

export default function Record() {
    const navigate = useNavigate();
    const handleFinish = (values) => {
        const data = { ...values, date: dayjs(values.date).format('YYYY-MM-DD') }
    }
    return (
        <div>
            <Form layout='horizontal'
                footer={
                    <Button block type='submit' color='primary' size='large'>
                        提交
                    </Button>
                } initialValues={{
                    date: new Date(),
                    module: 'xingnce',
                    taskType: 'exam',
                }} onFinish={handleFinish}>
                {/* 日期选择 */}
                <Form.Item
                    name='date'
                    label='日期'
                    trigger='onConfirm'
                    onClick={(e, datePickerRef) => {
                        datePickerRef.current?.open()
                    }}
                >
                    <DatePicker>
                        {value =>
                            value ? dayjs(value).format('YYYY-MM-DD') : '请选择日期'
                        }
                    </DatePicker>
                </Form.Item>

                {/* 学习模块选择 */}
                <Form.Item label="学习模块" name='module'>
                    <Radio.Group>
                        <Space>
                            {
                                moduleColumns.map((item, index) => (
                                    <Radio key={index} value={item.value}>
                                        {item.label}
                                    </Radio>
                                ))
                            }
                        </Space>
                    </Radio.Group>
                </Form.Item>

                {/* 任务类型选择 */}
                <Form.Item name='taskType' label="任务类型">
                    <Radio.Group>
                        <Space wrap>
                            {
                                taskTypeColumns.map((item, index) => (
                                    <Radio key={index} value={item.value}>
                                        {item.label}
                                    </Radio>
                                ))
                            }
                        </Space>
                    </Radio.Group>
                </Form.Item>


                <Form.Item label="正确题数" name='correct_number'>
                    <Input type='number' placeholder="请输入题数" />
                </Form.Item>

                <Form.Item label="总题数" name='total_number'>
                    <Input type='number' placeholder="请输入题数" />
                </Form.Item>

                <Form.Item label="正确率" shouldUpdate={(prev, curr) => prev.correct_number !== curr.correct_number || prev.total_number !== curr.total_number}>
                    {
                        ({ getFieldValue }) => {
                            const correct_number = getFieldValue('correct_number')
                            const total_number = getFieldValue('total_number')

                            if (!correct_number || !total_number) return ''
                            return (
                                Math.round(correct_number * 100 / total_number) + '%'
                            )
                        }
                    }
                </Form.Item>

                <Form.Item name='score' label="模考分数">
                    <Input type='number' placeholder="请输入分数" />
                </Form.Item>

                <Form.Item name='remark' label="备注">
                    <TextArea placeholder="问题/心得" rows={3} />
                </Form.Item>
            </Form>
        </div>
    )
}